.rmu__features{
width: 100%;
display: flex;
justify-content: space-between;
flex-direction: row;
margin: 4rem 6rem;
}

.rmu__features-heading{
flex: 1;
display: flex;
justify-content: flex-start;
flex-direction: column;
text-align: left;
margin-right: 5rem;
}

.rmu__features-heading h1 {
    font-family: var(--font-family);
    font-weight: 800;
    font-size: 34px;
    line-height: 45px;
    } 
    .rmu__features-heading p {
        font-family: var(--font-family);
        font-weight: 500;
        font-size: 16px;
        line-height: 30px;
        color: #ff8a71;
        margin-top: 2rem;
        } 

        .rmu__features-container {
            flex: 1.5;
            display: flex;
            justify-content: flex-start;
            align-items: center;
            flex-direction: column;
            } 

 @media screen and (max-width: 990px){

    .rmu__features{
        
        flex-direction: column;
        } 
        .rmu__features-heading{
            margin: 0 0 2rem 0;
            }
                    
}


@media screen and (max-width: 550px){

        .rmu__features-heading h1{
           
            font-size: 28px;
            line-height: 38px;
            }
                    
}