.rmu__possibility {
    display: flex;
    flex-direction: row;
    margin: 4rem 6rem;
    

}

.rmu__possibility-image{
    flex: 1;
    display: flex;
    justify-content: flex-start;
    align-items: center;
    margin-right: 2rem;

}

.rmu__possibility-image img{

width: 100%;
height: 100%;


}
.rmu__possibility-content{
    display: flex;
    flex: 1;
    flex-direction: column;
    justify-content: center;
    align-items: flex-start;
    margin-top: 15rem;

    
  
}

.rmu__possibility-content h4{
    /* width: 16.9375rem;
    height: 1.875rem; */
   
    font-family: var(--font-family);
   
    font-weight: 500;
    font-size: 16px;
    line-height: 30px;
  
    color: #71E5FF;
    margin-bottom: 2rem;
    
    
}

.rmu__possibility-content h4:last-child{
    /* width: 16.9375rem;
    height: 1.875rem; */
   
    font-family: var(--font-family);


font-weight: 500;
font-size: 16px;
line-height: 30px;

color: #FF8A71;

    
}
.rmu__possibility-content h1{
    /* width: 26.625rem;
height: 5.625rem; */


font-family: var(--font-family);
font-weight: 800;
font-size: 34px;
line-height: 45px;
margin-bottom: 2rem;
    
}
.rmu__possibility-content p{
/* width: 29.9375rem;
height: 7.5rem; */
font-family: var(--font-family);
font-size: 16px;
line-height: 30px;

color: var(--color-text);
margin-bottom: 2rem;
}

@media screen and (max-width: 950px){
    .rmu__possibility {
       
        flex-direction: column;
    
    }
    .rmu__possibility-image img{

       margin: 1rem 0;
        
        
        }

        .rmu__possibility-content{
           
            margin-top: 2rem;

        }
}