.rmu__blog{
    display: flex;
    flex-direction: column;
}
.rmu__blog-heading {
    width: 100%;
    text-align: left;
    margin-bottom: 5rem;
}
.rmu__blog-heading h1{
    font-family: Var(--font-family);
font-weight: 800;
font-size: 62px;
line-height: 75px;
letter-spacing: -0.04em;
}

.rmu__blog-container{
    display: flex;
    flex-direction: row;
}
.rmu__blog-container_groupA{
    flex: .75;
    margin-right: 2rem;

}

.rmu__blog-container_groupB{
    flex: 1;
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    grid-gap: 2rem;
}

@media screen  and (max-width:960px){
    .rmu__blog-container{
       
        flex-direction: column-reverse;
    }
    .rmu__blog-container_groupA{
       
        margin: 2rem 0;
    
    }
    .rmu__blog-container_groupA .rmu__blog-container_article{
       
    width: 48%;
    
    }
    .rmu__blog-container_groupA .rmu__blog-container_article-image{
       
     height: 250px;
        
        }
}

@media screen  and (max-width:700px){
    
    .rmu__blog-container_groupB{
       
      grid-template-columns: repeat(1 ,1fr);
    
    }
    .rmu__blog-container_groupA .rmu__blog-container_article{
       
        width: 100%;
        
        }
        .rmu__blog-heading h1{
            
        
        font-size: 56px;
        line-height: 52px;
        
        }
}
@media screen  and (max-width:550px){
    
   
        .rmu__blog-heading h1{
            
        
        font-size: 34px;
        line-height: 42px;
        
        }
}