.rmu__footer{
    display: flex;
    background: var(--color-footer);
    
}


.rmu__footer-container_heading{
  width: 100%;
  text-align: center;
  margin-bottom: 3rem;
}
.rmu__footer-container_heading h1{
font-family: var(--font-family);
font-weight: 800;
font-size: 62px;
line-height: 75px;
letter-spacing: -0.04em;
color: #fff;
}

.rmu__footer-container-btn {
    display: flex;
    justify-content: center;
    align-items: center;
    padding: 1rem;
    border: 1px solid #fff;
    text-align: center;
    margin-bottom: 10rem;
    cursor: pointer;
    
    
    }
.rmu__footer-container-btn p{
font-family: var(--font-family);
font-size: 18px;
line-height: 21px;
color: #fff;
/* word-spacing: 1rem; */


}
.rmu__footer-container-content{
    display: flex;
  
    justify-content: space-between;
    flex-wrap: wrap;
    align-items: flex-start;
    flex-direction: row;
    width: 100%;
    text-align: left;
}
.rmu__footer-container-content_div{
    display: flex;
   width: 250px;
   margin: 1rem;
}

.rmu__footer-container-content_logo{
    display: flex;
    flex-direction: column;
}

.rmu__footer-container-content_logo img{
    width: 118px;
    height: 30px;
    margin-bottom: 1rem;

} 
.rmu__footer-container-content_logo p{
    font-family: var(--font-family);
font-size: 12px;
line-height: 14px;
color: #fff;
}
.rmu__footer-container-content_div{
    display: flex;
 justify-content: flex-start;
    flex-direction: column;
   
   
}

.rmu__footer-container-content_div h4{
    font-family: var(--font-family);
    font-size: 14px;
    line-height: 16px;
    color: #fff;
    margin-bottom: 1rem;

   }

   .rmu__footer-container-content_div p{
       font-family: var(--font-family);
    font-size: 12px;
    line-height: 14px;
    color: #fff;
    cursor: pointer;
    margin: .5rem 0;
    
    }

    .rmu__footer-copywright{
        width: 100%;
        font-family: var(--font-family);
        font-size: 12px;
        line-height: 14px;
        color: #fff;
       text-align: center;
       margin-top: 2rem;
        
        }
        .rmu__footer-copywright p{
           
            font-family: var(--font-family);
            font-size: 12px;
            line-height: 14px;
            color: #fff;
          
            
            }
@media screen and (max-width:850px){
    .rmu__footer-container_heading h1{
        
      
        font-size: 44px;
        line-height: 50px;
       
        }
}

@media screen and (max-width:550px){
    .rmu__footer-container_heading h1{
        
      
        font-size: 34px;
        line-height: 42px;
       
        }
        .rmu__footer-container-content_div {
           
         margin: 1rem 0;
         
         }
     
        .rmu__footer-container-btn p{
         
            font-size: 14px;
            line-height: 42px;
            
            
            
            }
}
