.rmu__cta{
    display: flex;
    background: var(--gradient-bar);
    margin: 4rem ;
    border: 1px solid #000000;
flex-direction: row;
border-radius: 1rem;
justify-content: space-between;

}

.rmu__cta-content{
  margin: 1rem ;
    

}

.rmu__cta-content p{
 
font-family: var(--font-family);
font-weight: 800;
font-size: 24px;
line-height: 45px;


color: #000000;

}

.rmu__cta-content h4{
    
    font-family: var(--font-family);
   
    font-weight: 500;
    font-size: 12px;
    line-height: 30px;
 
    color: #0E0E0E;
}

.rmu__cta-content-button {
   display: flex;
   justify-content: center;
   align-items: center;
    margin: 2rem;
    
}


.rmu__cta-content-button button{
width: 12rem;
height: 4rem;
 background: #000000;
border-radius: 40px;
font-family: var(--font-family);

font-weight: bold;
font-size: 18px;
line-height: 30px;
color: #FFFFFF;
cursor: pointer;
border: none;
outline: none;
}

@media screen and (max-width: 650px){
    .rmu__cta{
       
    flex-direction: column;
    
    
    }

    .rmu__cta-content-button {
        
         margin: 2rem ;
         
     }
}
@media screen and (max-width: 550px){
    .rmu__cta{
       
    margin: 4rem 2rem;
    
    
    }

    .rmu__cta-content p{
        
       font-size: 18px;
       line-height: 32px;
        
    }

    .rmu__cta-content-button button{
        
        font-size: 14px;
        line-height: 28px;
         
     }
}