.rmu__header{
    display: flex;
}
.rmu__header-content{
    flex: 1;
    display: flex;
    justify-content: center;
    align-items: flex-start;
    flex-direction: column;
    margin-right: 5rem;
}
.rmu__header-content h1{
    font-family: var(--font-family);
    font-weight: 900;
    font-size: 62px;
    line-height: 75px;
    letter-spacing: -0.04em;
}
.rmu__header-content p{
    font-family: var(--font-family);
    font-style: normal;
    font-weight: normal;
    font-size: 20px;
    line-height: 27px;
    color: var(--color-text);
    margin-top: 1.5rem;
}
.rmu__header-content__input{
    width: 100%;
    margin: 2rem 0 1rem;
    display: flex;
}
.rmu__header-content__input input{
   flex: 2;
   width: 100%;
   min-height: 50%;
   font-family: var(--font-family);
    font-size: 20px;
    line-height: 27px;
    background: var(--color-footer);
    border: 2px solid  var(--color-footer);
    padding: 0 1rem;
    outline: none;
    color: #fff;
    border-top-right-radius: 5px;
    border-bottom-right-radius: 5px;
}

.rmu__header-content__input button{
  flex: 0.6;
  width: 100%;
   min-height: 50%;
   font-family: var(--font-family);
   font-size: 20px;
    line-height: 27px;
    font-weight: bold;
    background: #ff4820;
    color: #fff;
    cursor: pointer;
    outline: none;
    padding: 0 1rem;
    border: none;
    border-top-right-radius: 5px;
    border-bottom-right-radius: 5px;

 }

 .rmu__header-content__people{
    width: 100%;
    display: flex;
    justify-content: flex-start;
  align-items: center;
  margin-top: 2rem;
   }

   .rmu__header-content__people img{
    width: 182px;
    height: 38px;
   }
   .rmu__header-content__people p{
    margin:0 0 0 1rem;
    font-family: var(--font-family);
   font-size: 12px;
    line-height: 38px;
    font-weight: 500;
    color: #fff;
    text-align: center;
   }

   .rmu__header-content__image{
    flex: 1;
    display: flex;
    justify-content: center;
    align-items: center;
   }

   .rmu__header-content__image img{
    width: 100%;
    height: 100%;
    -moz-box-shadow: 1px 2px 3px rgba(0,0,0,.5);
-webkit-box-shadow: 1px 2px 3px rgba(0,0,0,.5);
box-shadow: 1px 2px 3px rgba(0,0,0,.5);
   }

   @media screen and (max-width: 1050px){

       .rmu__header{
    flex-direction: column;
}
.rmu__header-content{
   margin: 0  0 3rem;
}
   }

   @media screen and (max-width: 650px){

    .rmu__header h1{ 
font-size: 48px;
line-height: 60px;
}
.rmu__header p{ 
    font-size: 16px;
    line-height: 24px;
    }

    .rmu__header-content__people{
       flex-direction: column;
       }

       .rmu__header-content__people p{
        margin: 0;
        }
        .rmu__header-content__input,
        .rmu__header-content__button
        {
            font-size: 16px;
            line-height: 24px;
        }
}

@media screen and (max-width: 490px){
    .rmu__header h1{ 
        font-size: 36px;
        line-height: 48px;
        }
        .rmu__header p{ 
            font-size: 14px;
            line-height: 24px;
            }

     .rmu__header-content__input,
        .rmu__header-content__button
        {
            font-size: 12px;
            line-height: 16px;
        }
        

}