.rmu__navbar{
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 2rem 6rem;
}
.rmu__navbar-links{
    flex: 1;
    display: flex;
    justify-content: flex-start;
    align-items: center;

}

.rmu__navbar-links_logo{

margin-right: 2rem;
}

.rmu__navbar-links_logo img{

    width: 80px;
    height: 80px;
    }
.rmu__navbar-links_container{

        display: flex;
        flex-direction: row;
        }
.rmu__navbar-sign{

            display: flex;
            justify-content: flex-end;
            align-items: center;
            }

.rmu__navbar-links_container p,
.rmu__navbar-sign p, 
.rmu__navbar-menu_container p{

                color: white;
                font-family: var(--font-family);
                font-weight: 500;
                font-size: 18px;
                line-height: 25px;
                text-transform: capitalize;
                margin: 0 1rem;
                cursor: pointer;
                }


.rmu__navbar-sign button, 
.rmu__navbar-menu_container button{
padding: 0.5rem 1rem;
color: white;
background: #ff4820;
font-family: var(--font-family);
font-weight: 500;
font-size: 18px;
line-height: 25px;
border-radius: 5px;
border: none;
outline: none;
cursor: pointer;
                
                }
.rmu__navbar-menu{
   margin-left: 1rem;    
   display: none; 
   position: relative;          

}
.rmu__navbar-menu svg{
    cursor: pointer;         
 
 }
 .rmu__navbar-menu_container{
    display: flex;    
    justify-content: flex-end;   
    align-items: flex-end; 
    flex-direction: column;
    text-align: end;
    background: var(--color-footer);
    padding: 2rem;
    position: absolute;
    top: 40px;
    right: 0;
    margin-top: 1rem;
    min-width: 210px;
    border-radius: 5px;
    box-shadow: 0 0 5 rgba(0, 0, 0, 0.2);
 
 }
 .rmu__navbar-menu_container p{
    margin: 1rem 0;
 
 }
 .rmu__navbar-menu_container-links-sign{
  display: none;
 
 }
 @media screen and (max-width: 1050px) {
    .rmu__navbar-links_container {
        display: none;
     
     }
     .rmu__navbar-menu{
        display: flex;
     
     }
 }

 @media screen and (max-width: 700px) {
    .rmu__navbar {
       padding: 2rem 4rem;
     
     }
    
 }
 @media screen and (max-width: 550px) {
    .rmu__navbar {
       padding: 2rem ;
     
     }
     .rmu__navbar-sign {
        display: none;
      
      }
      .rmu__navbar-menu_container {
        top: 20px;
      
      }
      .rmu__navbar-menu_container-links-sign {
        display: block;
      
      }
    
 }


